


























































































	import uniSteps from '../../components/uni-steps/uni-steps.vue'
	import {
		mapState,
		mapMutations,	
	} from 'vuex';
	
	export default{
		components: {
			uniSteps
		},
		computed: mapState([ 'hasLogin','userInfo']),
	data(){
		return{
			active:1,
			showClearIcon: false,
			obj:{},
			id:0,
			info:[],
			other_opts:[],
			collect:'',
			hbsj:'',
			page_down:'',
			view_product:'',
			list2: [
			{
				title: '搜索宝贝',
				
			}, 
			{
				title: '找到宝贝后先货比,在按照商家要求完成下单',
				
			}, 
			{
				title: '买家签收',
				
			}, 
			{
				title: '交易完成',
				
			},
				]
		}
	},
	
	onLoad:function(e){
		this.id = e.id;
			uni.request({
				url:`${this.$serverUrl}/task/order_process?id=`+e.id,
				header:{
					"Content-Type": "application/x-www-form-urlencoded",
					"token":this.userInfo.token
				},
				method:"GET",
				success:(res)=>{
					this.info = res.data
					this.other_opts = res.data.other_opts
				}
			})
	
		
	},
	onUnload() {
		this.collect = '';
	},
	methods:{
		clearIcon: function() {
			this.inputClearValue = '';
			this.showClearIcon = false;
		},
		clearInput: function(event) {
			this.inputClearValue = event.target.value;
			if (event.target.value.length > 0) {
				this.showClearIcon = true;
			} else {
				this.showClearIcon = false;
			}
		},
		chooseImage:function(opts) {
			
			uni.chooseImage({
				
				count: 1,
				sizeType: ['compressed','original'],
				sourceType: ['album'],
				success: (res) => {
					   const tempFilePaths = res.tempFilePaths[0]
					
					
					uni.uploadFile({
						header:{
							"token":this.userInfo.token
						},
						url: 'http://t2.qingxinedu.cn/api/task/upload',
						filePath: tempFilePaths,
						fileType: 'image',
						name: 'file',
						success: (e) => {
							
							var data = JSON.parse(e.data)
							
							if(data.status===1){
								
									if(opts=='hbsj'){
										this.hbsj = data.path
										this.obj.hbsj = data.path
									}else if(opts=='collect'){
										this.collect = data.path
										this.obj.collect = data.path
									}else if(opts=='page_down'){
										this.page_down = data.path
										this.obj.page_down = data.path
									}else if(opts=='view_product'){
										this.view_product = data.path
										this.obj.view_product = data.path
									}
									
									uni.showToast({
										title: '上传成功',
										icon: 'success',
										duration: 1000
									})
									
									/* if(this.image_list==[]){
										this.image_list[opts]  = data.path;
										this.obj[opts] = data.path
									}else{
										this.image_list[opts] = this.image_list.concat(data.path)
										this.obj.opt = data.path
									} */
										console.log(this.obj)
							}
							
						},
						fail: (err) => {
							console.log('uploadImage fail', err);
							uni.showModal({
								content: err.errMsg,
								showCancel: false
							});
						}
					});
				},
				fail: (err) => {
					console.log('chooseImage fail', err)
				}
			})
			
		},
		 formSubmit: function(e) {
			var data = e.detail.value;	
			 uni.request({
				 header:{
				 	"Content-Type": "application/x-www-form-urlencoded",
				 	"token":this.userInfo.token
				 },
			 	url:`${this.$serverUrl}/task/order_view?id=`+this.id,
				data:{
					"process_content":JSON.stringify(this.obj),
					"sn":data.sn,
					},
				method:"POST",
				success: (res) => {
					if(res.data.status==1){
						uni.reLaunch({
							url:"task"
						})
					}
				}
			 })
            
        },
	},
	}
